@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  background: url(/React-Order-summary/static/media/pattern-background-desktop.5a0ab689.svg);
  background-size: 100%;
  background-repeat: no-repeat;

  margin: 0;
  background-color: hsl(225, 100%, 94%);
}

.card{
  width: 21rem;
  margin: auto;
  margin-top: 10rem;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

img,.card{
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
@media (min-width:992px){
  .card{
    width: 22rem;
  }
}
.card-title{
    margin-top: 1rem;
    margin-bottom: 1rem;
    text-align: center;
    font-weight: 700;
    font-size: x-large;

}
.card-text{
    text-align: center;
    padding: 0rem 0.5rem;
    opacity: .5;
    font-weight: 500;
    font-size: 16px;
}
.btns{
    text-align: center;
}
.btn-primary-custom{
    background: hsl(245, 75%, 52%);
    color: white;
    font-size: 14px;
    font-weight: 500;
    width: 95%;
    padding: 0.7rem 0rem;
    border-radius: 12px;
    transition: .50s all;
}
.btn-primary-custom:hover{
    opacity: .5;
    color: white;
    
}

.btn-cancel{
    font-size: 14px;
    font-weight: bold;
    width: 90%;
    padding: 0.5rem 0rem;
    opacity: .5;
    transition: .50s all;
}
.btn-cancel:hover{
    opacity: 1;
}
.pricing{
    margin: 1.5rem 0rem;
    padding: 0.8rem 0.8rem;
    background:   hsl(225, 100%, 98%);
    border-radius: 15px;
    display: flex;
    align-items: center;
}
.price{
    opacity: .5;
}
h6:not(.price){
    margin-bottom: 0.2rem;
    font-weight: 700;
}
a{
    color: blue;
    font-weight: 600;
}
